.ticket {
  position: relative;
  background-color: $color-bg-secondary;


  &::after {
    content: "";
    position: absolute;
    bottom: -95px;
    right: -110px;
    width: 322px;
    height: 385px;
    background-image: url("../img/weight-plates-desktop.png");
    transform: rotate(-30deg);

    @include vp-1199 {
      bottom: -73px;
      right: -10px;
      width: 290px;
      height: 345px;
      background-image: url("../img/weight-plates-tablet.png");
    }

    @include vp-767 {
      display: none;
    }
  }
}

.ticket__wrapper {
  padding-top: 120px;
  padding-bottom: 120px;
  display: flex;
  flex-direction: column;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 65px;
    padding-bottom: 75px;
  }
}

.ticket__wrapper h2 {
  @include text-alumnisans-54-65-600;

  margin-top: 0;
  margin-bottom: 0;
  align-self: center;
}
