.events {
  padding-top: 120px;
  padding-bottom: 55px;

  @include vp-1199 {
    padding-top: 95px;
    padding-bottom: 60px;
  }

  @include vp-767 {
    padding-top: 70px;
  }
}

.events__wrapper {
  background-color: $color-bg-banner;
  display: grid;
  grid-template-columns: 1fr 660px;

  @include vp-1199 {
    grid-template-columns: 1fr 386px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.events__description {
  padding: 85px 80px 90px;
  display: grid;
  grid-template-columns: 1fr 90px;
  column-gap: 20px;

  @include vp-1199 {
    padding: 50px 45px 25px;
    grid-template-columns: 1fr 55px;
    align-items: start;
    grid-template-rows: min-content;
  }

  @include vp-767 {
    padding: 45px;
  }
}

.events__description h3 {
  @include text-alumnisans-82-78-600;
  color: $color-text-white;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 10px;

  @include vp-767 {
    margin-top: 5px;
  }
}

.events__description dl {
  color: $color-text-white;
  display: flex;
  flex-direction: column;
  margin-top: 0;
  margin-bottom: 0;
}

.events__description dt {
  @include text-roboto-18-27-400;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid $color-default-white;

  @include vp-1199 {
    @include text-roboto-12-18-400;
    padding-bottom: 0;
  }
}

.events__date {
  @include text-roboto-14-21-400;
  display: flex;
  gap: 7px;
  padding: 0;
  margin: 0;
  margin-bottom: 5px;

  @include vp-1199 {
    @include text-roboto-10-15-400;
    margin-bottom: 0;
    gap: 4px;
  }
}

.events__date span {
  @include text-roboto-40-60-700;

  @include vp-1199 {
    @include text-roboto-24-36-700;
    margin: 0;
    padding: 0;
  }
}

.events__time {
  @include text-roboto-40-60-700;
  margin: 0;
  padding: 0;

  @include vp-1199 {
    @include text-roboto-24-36-700;
  }
}

.events__description p {
  @include text-roboto-18-27-400;
  grid-column: 1 / 3;
  color: $color-text-white;
  margin-bottom: 0;
  margin-top: 0;

  @include vp-1199 {
    @include text-roboto-12-18-400;
    margin-top: 0;
  }

  @include vp-767 {
    margin-bottom: 30px;
  }
}

.events__description button {
  grid-column: 1 / 3;
  margin-top: auto;

  @include vp-1199 {
    padding-top: 12px;
    padding-bottom: 12px;
    margin-top: 0;
    margin-bottom: auto;
  }

  @include vp-767 {
    margin-bottom: 0;
  }
}
